import React from 'react'
import PropTypes from 'prop-types'
import DeviceInput from 'components/forms/DeviceInput'
import stringImage from 'assets/connectionString.jpg'
import './HomeView.scss'

export const HomeView = ({ deviceName }) => (
  <div className={`container base`}>
    <br />
    <h1>Instruction to add to the PowerBI iothub</h1>
    <ol>
      <li>Run: <code>npm install -g iothub-explorer</code></li>
      <li>Input your Device Name here: <DeviceInput /> </li>
      <li>Run: <code>iothub-explorer create {deviceName} --connection-string
        --login
        "HostName=nodebots-iothub.azure-devices.net;SharedAccessKeyName=device;SharedAccessKey=a8LgD/Ogw8tguIGDm7apM9vsij8i4CewWnPlYOta1+4="
      </code>
      </li>
      <li>Take a look at the result of the command, and find the connectionString property. See example below.
      <img src={stringImage} alt='example' />
      </li>
      <li>Copy and Paste the connectionString value into your Competition NodeJS app. <br /> It should be something like
      <code>HostName=nodebots-azure...SharedAccessKey=...=</code></li>
      <li>Restart your app</li>
      <li>After a few moments you should see your device on the dashboard</li>
    </ol>
  </div>
)

HomeView.propTypes = {
  deviceName: PropTypes.string
}

export default HomeView



