import { connect } from 'react-redux'
import DeviceInput from './DeviceInput'
import { inputChangeDeviceName } from 'actions/input'
import { get } from 'lodash'

const mapStateToProps = (state) => {
  return {
    value: get(state, 'input.device_name.value', '')
  }
}

const mapDispatchToProps = (dispatch) => ({
  onChange: (evt) => {
    dispatch(inputChangeDeviceName(evt.target.value))
  }
})

const DeviceInputContainer = connect(mapStateToProps, mapDispatchToProps)(DeviceInput)

export default DeviceInputContainer



