import React from 'react'
import PropTypes from 'prop-types'
import { classNames } from 'support/helpers'
import styles from './DeviceInput.scss'

const DeviceInput = ({ className, onChange, value }) => (
  <div className={classNames([styles.base, className])}>
    <input className='input' type='text' placeholder='Device Name' value={value} onChange={onChange} />
  </div>
)

DeviceInput.propTypes = {
  className: PropTypes.string,
  onChange: PropTypes.func,
  value: PropTypes.string
}

export default DeviceInput



